import {
	Component,
	Input,
	ViewEncapsulation,
	AfterViewInit,
	OnDestroy,
	ElementRef,
	HostListener

} from '@angular/core';

import {
	Router,
	NavigationStart,
	NavigationEnd
} from '@angular/router';

import { AlphaHeader } from './util/menu-component';



export class AlphaGlobalHeader implements AfterViewInit, OnDestroy {

	public header:AlphaHeader;

	 home: Array<string>;
	 search: boolean;
	 searchAction: string;
	 languages: Array< any >;

	/**
	 * Close menu when clicking on self link
	 *
	 * @param event
	 */
	 onClick( event ) {

		const targ:HTMLElement = event.target,
			  isRouterLink:boolean = targ.hasAttribute( 'href' );

		if ( isRouterLink ) {
			// close the nav (should close by itself but when clicking on own items no navigation occurs)
			this.header.close();
		}

	}

	public constructor(
		private elRef: ElementRef,
		private router: Router
	) {

		/**
		 * Listen to navigation events
		 * Toolbar items that get moved around lose their ability to have their classes added / removed by angular
		 */
		this.router.events.subscribe(event => {

			if ( ! this.header ) {
				return;
			}

			if ( event instanceof NavigationStart ) {

				// close any open nav
				this.header.close();

			}

		})
	}


	/**
	 * After view and content has been rendered, check the menu widths
	 */
	public ngAfterViewInit(): void {

		// use our common menu sizing lib
		this.header = new AlphaHeader( this.elRef.nativeElement, {

			search : this.search ? { action : this.searchAction } : false,
			languages: this.languages

		} );

	}

	/**
	 * Cleanup on destroy
	 */
	public ngOnDestroy(): void {

		this.header.destroy();
	}

static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
	selector : 'alpha-global-header',
	template : `
	<a *ngIf="home" class="question-mark agh-icon-logo" id="mobileLogo" [routerLink]="home"></a>

	<div class="menu-container">

		<div *ngIf="home" class="before-menu">
			<a class="question-mark agh-icon-logo" [routerLink]="home"></a>
		</div>

		<div class="menu-area">

			<ul menu-container class="main-menu">

				<ng-content></ng-content>

			</ul>

		</div>

	</div>

  	`,
	styleUrls: ['../../../assets/less/styles/header.less'],
	encapsulation : ViewEncapsulation.None
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: ElementRef, },
{type: Router, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'home': [{ type: Input, args: ['home', ] },],
'search': [{ type: Input, args: ['search', ] },],
'searchAction': [{ type: Input, args: ['search-action', ] },],
'languages': [{ type: Input, args: ['languages', ] },],
'onClick': [{ type: HostListener, args: ['click', ['$event'], ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
